<script>
    import {AtomSpinner} from 'epic-spinners';
    import {mapGetters} from 'vuex';
    import bFormSelect from 'bootstrap-vue/es/components/form-select/form-select';
    import $ from 'jquery'
    import {EventBus} from '../main';
    import {saveFile} from '../mixins/saveFile'

    export default {
        mixins: [saveFile],
        data: function () {
            return {
                loading: true,
                listAction: false,
                paramCard: this.$store.getters.getParamCard,
                main_data_models: null,
                form_parameters: null,
                dependent: null,
                sets: null,
                file_data_model: 'DbAreaFiles',
                last_chosen: null
            }
        },

        methods: {

            /**
             toggle раздела
             */
            sectionClick(e) {
                var r = this.ftoggle($(e.target).parents('.faq-el-section').next());
                if (r) {
                    $(e.target).parents('.faq').addClass('active')
                } else {
                    $(e.target).parents('.faq').removeClass('active');
                }
            },


            /**
             Функция тогла show\hide
             */
            ftoggle(el, sec) {
                var res = $(el).css('display') == 'none' ? true : false
                if (res) {
                    $(el).show(sec || 300);
                } else {
                    $(el).hide(sec || 300);
                }
                return res;
            },


            /**
             Обращение к серверу при created для получения данных
             * @param {string} route - адрес для выполнения запроса
             */
            async fillComponent(route) {

                try {

                    this.loading = true; /* показываем прелоадер на время зашгрузки */

                    this.paramCard.id = (this.step_l) ? this.row_id_fe_route_step_object : this.$bRoute.params[this.$bRoute.meta.id_field];

                    /*
                    если прилетает пропс из шагов - передаем его как зависимый элемент
                    */
                    if (this.controller_alias) {
                        this.paramCard.dependent = {
                            dependent_data_model: this.controller_alias,
                            id: this.$bRoute.params[this.$bRoute.matched[0].meta.id_field],
                        };
                    }

                    /*
                        если есть роут-родитель, передаем его controller_alias и id обьекта от которого зависим данный компонент
                      */
                    if (this.$bRoute.meta.prev_controller_alias) {
                        this.paramCard.dependent = {
                            controller_alias: this.$bRoute.meta.prev_controller_alias,
                            id: this.$bRoute.params[this.$bRoute.meta.prev_id_field]
                        }
                    }

                    var res = await this.$http.post(route, this.paramCard);

                    this.main_data_models = res.data.main_data_models;
                    this.form_parameters = res.data.form_parameters;
                    this.dependent = res.data.dependent;
                    this.sets = res.data.sets;
                    this.loading = false; // убираем прелоадер
                    if (this.last_chosen) {

                        let elem = document.getElementById(this.last_chosen);
                        if (elem) {
                            $(elem).show(300);
                        }
                    }

                } catch (error) {
                    this.$toast.error(error.response.data.message, '' + error.response.status, {position: 'topRight'})
                    this.loading = false; // убираем прелоадер
                }
            },


            /*
             Прикрепление файла + отправка на сервер для сохранения
             * @param {object} e - event object.
             * @param {object} item - вид документа, на котором произошло добавление файла
             */
            async fileUpload(e, item) {
                var reader = new FileReader();

                if (e.target.files[0]) {

                    this.last_chosen = 'section_' + item.att_doc_kind_name;

                    const file_data_model = this.file_data_model; // имя модели данных, объекты которой будем сохранять
                    var name = e.target.files[0].name;
                    name = name.slice(0, name.lastIndexOf('.'));

                    var main_data_models = {};

                    main_data_models[file_data_model] = [JSON.parse(JSON.stringify(item['db_area_files'][0]))]; // копируем пустую модель

                    main_data_models[file_data_model][0]['db_area_file_name'] = name; // вставляем имя файла

                    /*
                    заполняем модель stored_file'a
                     */
                    var obj = {
                        id: null,
                        storedFile: {
                            name: name,
                            extension: e.target.files[0].name.slice(e.target.files[0].name.lastIndexOf(".") + 1),
                            size: e.target.files[0].size,
                        }
                    };


                    reader.readAsDataURL(e.target.files[0]); // считываем файл

                    this.$validator.validate(item.att_doc_type_name).then(async res => {
                        if (res) {
                            reader.onload = async (e) => {
                                var mime = e.target.result.split(',');
                                obj['storedFile']['mime'] = mime[1];
                                obj['storedFile']['mime_type'] = mime[0];
                                main_data_models[file_data_model][0]['stored_file'] = obj;

                                try {
                                    var res = await this.$http.post('/admin/dbArea/file/write', {
                                        main_data_models: main_data_models,
                                        form_parameters: {form_base_data_model: file_data_model}
                                    });

                                    await this.fillComponent(this.$bRoute.meta.route);
                                    document.getElementById(item.att_doc_kind_name).value = ''; //удаляем загруженный файл из инпута
                                } catch (error) {
                                    this.$toast.error(error.response.data.message, '' + error.response.status, {position: 'topRight'})
                                }


                            }
                        }
                    });
                }
            },
            deleteFile(id, item) {
                this.fillComponent(this.$bRoute.meta.route)
//
            },
        },

        props: ['row_id_fe_route_step_object', 'step_l', 'controller_alias'],

        components: {
            AtomSpinner,
            bFormSelect
        },

        async mounted() {
            this.imgtosvg();


            this.fillComponent(this.$bRoute.meta.route)


            EventBus.$on('close_step', async () => {

                var res = this.main_data_models['BlRequiredDocuments'].filter(item => item.required_document_l).every(item => item.db_area_files.length > 1);
                if (!res) {
                    this.$store.dispatch('showToast', {
                        template: 'error',
                        title: "Ошибка!",
                        message: "Прикрепите недостающие обязательные документы",
                    });
                    return;
                }
                this.$emit('modelSaved');

                // else {
                //     var action = this.action_buttons.filter(action => {
                //         return action.code === "apply"
                //     });
                //     var res = await this.saveModel(action[0]);
                //     if (res) this.$emit('modelSaved')
                // }

            });
        },

        updated() {
            this.imgtosvg();
        },

        beforeDestroy() {
            EventBus.$off('close_step');
        },


    }
</script>

